@font-face {
	font-family: 'icomoon';
	src: url('../../assets/icons/icomoon/icomoon.eot?81wixp');
	src: url('../../assets/icons/icomoon/icomoon.eot?81wixp#iefix')
			format('embedded-opentype'),
		url('../../assets/icons/icomoon/icomoon.ttf?81wixp') format('truetype'),
		url('../../assets/icons/icomoon/icomoon.woff?81wixp') format('woff'),
		url('../../assets/icons/icomoon/icomoon.svg?81wixp#icomoon') format('svg');
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

[class^='icon-'],
[class*=' icon-'] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	speak: never;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

